.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


html {
  background-image: url('https://i.ytimg.com/vi/3I7LjJ3-76s/maxresdefault.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}


header h1 {
  font-family: 'fantasy', helvetica, arial;
}

table {
  border-collapse: collapse;
}


td {
  width: 20px;
  height: 20px;
  border: 1px solid black;
  background-color: #C0C0C0;
  -webkit-box-shadow: inset 2px 2px 0px 0px #EEEEEE, inset -2px -2px 0px 0px #808080;
          box-shadow: inset 2px 2px 0px 0px #EEEEEE, inset -2px -2px 0px 0px #808080;
}

td.open.empty {
  background-color: #EEEEEE;
  border: 1px dashed #2d3e50;
  -webkit-box-shadow: none;
          box-shadow: none;
}

td.open.bomb {
  background-color: tomato;
  border: 1px dashed #2d3e50;
  -webkit-box-shadow: none;
          box-shadow: none;
}

td .cell_icon {
  max-height: 95%;
  max-width: 95%;
  -o-object-fit: contain;
     object-fit: contain;
}

.gameFlex {
  display: -ms-flexbox;
  display: flex;
  border: 1px dashed green;
}

.interface {
  -ms-flex: 1 1;
      flex: 1 1;
}

.board-container {
  -ms-flex: 2 1;
      flex: 2 1;
}

.leaderBoard {
  -ms-flex: 1 1;
      flex: 1 1;
}



