

.interface {
  background-color: #EEEEEE;
  padding: 20px 10px 0px 10px;
  -webkit-box-shadow: inset 3px 3px 0px 0px #808080, inset -3px -3px 0px 0px #ffffff;
          box-shadow: inset 3px 3px 0px 0px #808080, inset -3px -3px 0px 0px #ffffff;
}

.score {
  font-size: 1.5em;
  font-family: fantasy;
  padding: 20px;
  border: 2px solid #bec3c7;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  line-height: 50px;
  margin: auto;
}

.sizeButton {
  border: 1px solid #2d3e50;
  background-color: #fff;
  font-size: 1.1em;
  padding: 10px;
  border-radius: 20px;
  cursor: pointer;
}

.sizeButton.active {
  border: 1px solid tomato;
}




.interface .toggleReset {
  -ms-flex: 1 1;
      flex: 1 1;
  border: 1px solid rgb(53,152,219);
  font-size: 1.2em;
  padding: 10px 15px;
  border-radius: 10px;
  background-color: rgba(53,152,219, 0.2);
  -webkit-transition: .2s linear;
  -o-transition: .2s linear;
  transition: .2s linear;
  cursor: pointer;
}

.interface .toggleReset:hover {
  background-color: rgba(53,152,219, 0.3);
  -webkit-box-shadow: 5px 5px 20px rgba(0,0,0, 0.1);
          box-shadow: 5px 5px 20px rgba(0,0,0, 0.1);
}

.interface .toggleReset:active {
  background-color: rgb(53,152,219);
  -webkit-box-shadow: 2px 2px 10px rgba(0,0,0, 0.1);
          box-shadow: 2px 2px 10px rgba(0,0,0, 0.1);
  color: #ecf0f1;
}



